import React from 'react'
import Layout from '../components/layout'
import Masonry from 'react-masonry-component'

import './style/engagement.css'

import one from '../assets/images/engagement/HJ1_0153.jpg'
import two from '../assets/images/engagement/HJ1_0162.jpg'
import three from '../assets/images/engagement/HJ1_0168.jpg'
import four from '../assets/images/engagement/HJ1_0177.jpg'
import five from '../assets/images/engagement/HJ1_0184.jpg'
import six from '../assets/images/engagement/HJ1_0300.jpg'
import seven from '../assets/images/engagement/HJ1_0332.jpg'
import eight from '../assets/images/engagement/HJ1_0360 (1).jpg'
import nine from '../assets/images/engagement/HJ1_0370.jpg'
import ten from '../assets/images/engagement/HJ1_0445.jpg'
import eleven from '../assets/images/engagement/HJ1_0503.jpg'

const IMAGES = [
    one,
    two,
    three,
    four,
    five,
    six,
    seven,
    eight,
    nine,
    ten,
    eleven,
]

class Engagement extends React.Component {
    constructor(context, props) {
        super(context, props)
        this.state = {loaded: false}
        this.show = this.show.bind(this)
    }
    show() {
        if (!this.state.loaded) {
            this.setState({loaded: true})
        }
    }
    render() {
        const style = this.state.loaded ? {} : {'display': 'none'}
        return (
            <Layout>
                <div className='engagement'>
                    <div className='engagement-hero'>
                        <div className='engagement-hero-img'>
                            <span className='engagement-hero-text'>
                                Engagement
                            </span>
                        </div>
                    </div>

                    <div className='engagement-content'>
                        <Masonry style={style} onImagesLoaded={this.show}>
                            {IMAGES.map((img, i) => (
                                <div key={i} className='engagement-image'>
                                    <img src={img} alt='engagement' />
                                </div>
                            ))}
                        </Masonry>
                        {!this.state.loaded && (
                            <div className='bouncing-loader'>
                                <div />
                                <div />
                                <div />
                            </div>
                        )}
                    </div>
                </div>
            </Layout>
        )
    }
}

export default Engagement
